        img {
            width: 250px;
            height: 250px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: -1;
        }
    }
    div.container3 {
        width: 360px;
        height: 360px;
        position: relative;
        background: $dark_cyan;
        z-index: 1;
        border: 1px solid $dark_cyan;
