#circle1 {
    animation: changeColor1 1.5s infinite alternate;
}

@keyframes changeColor1 {
    0% {
        fill: #ff0000;
    }
    100% {
        fill: #ff8700;
    }
}

#circle2 {
    animation: changeColor2 1.5s infinite alternate;
}

@keyframes changeColor2 {
    0% {
        fill: #ffd300;
    }
    100% {
        fill: #0aefff;
    }
}

#circle3 {
    animation: changeColor3 1.5s infinite alternate;
}

